import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import commonSpread from '@/components/common/spread'
import commonRule from '@/components/common/rule'
import commonLogin from '@/components/common/login'

import userOrder from '@/components/user/order'
import userOrderAdd from '@/components/user/order_add'
import userOrderIndex from '@/components/user/order_index'
import userOrderInfo from '@/components/user/order_info'
import userOrderFinish from '@/components/user/order_finish'
import userOrderEnd from '@/components/user/order_end'
import userPersonalIndex from '@/components/user/personal_index'

import riderOrderGet from '@/components/rider/order_get'
import riderOrderIndex from '@/components/rider/order_index'
import riderOrderInfo from '@/components/rider/order_info'
import riderOrderMap from '@/components/rider/order_map'
import riderOrderFinish from '@/components/rider/order_finish'
import riderPersonalIndex from '@/components/rider/personal_index'

import siteOrderIndex from '@/components/site/order_index'
import siteSelectRider from '@/components/site/select_rider'
import siteComplainIndex from '@/components/site/complain_index'
import siteComplainInfo from '@/components/site/complain_info'
import sitePersonalIndex from '@/components/site/personal_index'

import serverOrder from '@/components/server/order'
import serverUser from '@/components/server/user'
import serverRule from '@/components/server/rule'
import serverAccount from '@/components/server/account'
import serverProxy from '@/components/server/proxy'
import serverComplain from '@/components/server/complain'
import serverRider from '@/components/server/rider'
import serverRiderOrder from '@/components/server/rider_order'
import serverRiderMoney from '@/components/server/rider_money'
import serverSite from '@/components/server/site'
import serverAgree from '@/components/server/agree'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: index
    },
    {
      path: '/user/order',
      component: userOrder
    },
    {
      path: '/user/order/add',
      component: userOrderAdd
    },
    {
      path: '/user/order/index',
      component: userOrderIndex
    },
    {
      path: '/user/order/info',
      component: userOrderInfo
    },
    {
      path: '/user/order/finish',
      component: userOrderFinish
    },
    {
      path: '/user/order/end',
      component: userOrderEnd
    },
    {
      path: '/user/personal/index',
      component: userPersonalIndex
    },
    {
      path: '/common/spread',
      component: commonSpread
    },
    {
      path: '/common/rule',
      component: commonRule
    },
    {
      path: '/common/login',
      component: commonLogin
    },
    {
      path: '/rider/order/get',
      component: riderOrderGet
    },
    {
      path: '/rider/order/index',
      component: riderOrderIndex
    },
    {
      path: '/rider/order/info',
      component: riderOrderInfo
    },
    {
      path: '/rider/order/map',
      component: riderOrderMap
    },
    {
      path: '/rider/order/finish',
      component: riderOrderFinish
    },
    {
      path: '/rider/personal/index',
      component: riderPersonalIndex
    },
    {
      path: '/site/order/index',
      component: siteOrderIndex
    },
    {
      path: '/site/select/rider',
      component: siteSelectRider
    },
    {
      path: '/site/complain/index',
      component: siteComplainIndex
    },
    {
      path: '/site/complain/info',
      component: siteComplainInfo
    },
    {
      path: '/site/personal/index',
      component: sitePersonalIndex
    },
    {
      path: '/server/order',
      component: serverOrder
    },
    {
      path: '/server/rider',
      component: serverRider
    },
    {
      path: '/server/rider/order',
      component: serverRiderOrder
    },
    {
      path: '/server/rider/money',
      component: serverRiderMoney
    },
    {
      path: '/server/user',
      component: serverUser
    },
    {
      path: '/server/site',
      component: serverSite
    },
    {
      path: '/server/proxy',
      component: serverProxy
    },
    {
      path: '/server/account',
      component: serverAccount
    },
    {
      path: '/server/agree',
      component: serverAgree
    },
    {
      path: '/server/complain',
      component: serverComplain
    },
    {
      path: '/server/rule',
      component: serverRule
    }
  ]
})



