<template>
  <div class="hello">
    <h2></h2>
    <div style="height:1080px;width:1920px;border:1px solid black;margin:0 auto;text-align:center">
      <div style="background:#ffca0a;text-align:center;padding:0px 40px;height:180px">
        <img src="../../assets/banner/banner_server.jpg" alt="banner">
      </div>
      <div>
        <div class="left">
          <div class="li-left"></div>
          <div class="li-left"></div>
          <div class="li-left"></div>
          <div class="li-left-now"></div>
          <div class="li-left"></div>
          <div class="li-left"></div>
          <div class="li-left"></div>
          <div class="li-left"></div>
          <div class="li-left"></div>
        </div>
        <div class="right">
          <h1 style="margin-left:50px"></h1>
          <div class="block"></div>
          <div class="block"></div>
          <div class="block"></div>
          <div class="block"></div>
          <div class="block"></div>
          <div class="block"></div>
        </div>
      </div>
    </div>
    <h3>made by 2021/6/26, using vue 2.5.2.<a href="#/"></a></h3>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld'
}
</script>

<style scoped>
.left{
  width: 15%;
  float: left;
  text-align: right;
  height: 900px;
  background: rgb(255,211,92,0.8);
}
.li-left{
  padding: 20px;
  cursor: pointer;
  text-align: center;
  font-size: 18px;
}
.li-left:hover{
  font-size: 20px;
  background: rgba(148, 145, 1, 0.2);
}
.li-left-now{
  padding: 20px;
  cursor: pointer;
  text-align: center;
  font-size: 20px;
  background: rgba(148, 145, 1, 0.2);}
.right{
  width: 84%;
  float: left;
  text-align: left;
  height: 900px;
}
.block{
  cursor: pointer;
  width: 100px;
  height: 100px;
  background: rgba(255,211,92,0.5);
  border: 5px solid rgba(148, 145, 1, 0.2);
  font-size: 20px;
  text-align: center;
  margin: 50px 0 50px 50px;
  border-radius: 20px;
  float: left;
  line-height: 100px;
  line-break: inherit;
}
.block:hover{
  background: rgba(255,211,92);
}
</style>



