}
.type-text{
  width: 33%;
  float: left;
  margin-left: 4%;
  margin-right: 4%;
  margin-top: 1%;
  margin-bottom: 1%;
  padding-left: 4%;
  padding-right: 4%;
  text-align: center;
}

</style>



